.form{
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 3.2rem;
  padding: 3.2rem;
  max-width: 144rem;
  margin: 0 auto;
}
.form-left{
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.form-right{
  background: var(--white);
  border-radius: 10px;
  padding: 2.4rem;
}